<template>
    <v-container class="justify-center px-12 mt-8 " >
        <div class="d-flex align-center">
            <div class="avatar-container" :class="[imgLoading || getLoading ? 'd-flex justify-center align-center' : '']">
                <span class="ct-load d-flex justify-center align-center" v-if='imgLoading'>
                  <img src="../assets/new_files/imgloading.gif" class='loading-gif' alt="loading">
                </span>
                <span v-else>
                  <v-avatar
                    size="100"
                  >
                    <img :src="getCandidate.avatar.url" v-if="getCandidate.avatar.url" alt="profile-img">
                    <img src="../assets/social.png" alt="upload-img" v-else>
                  </v-avatar>
                  <input type="file" name="file" id="fileUploadImage" class="inputfile" />
                  <label for="fileUploadImage">
                    <span class="upload_pic-btn" slot="append" type="file" >
                        <img src="../assets/new_files/upload.png" class="upload_img" alt="upload_image" >
                    </span>
                  </label>
                </span>
            </div>

            <div class="ml-8">
              <div class="ct-name font-weight-bold">{{ getCandidate.name }}</div>
            </div>
        </div>
  
        <div class="mt-8">
           <v-row class="mb-lg-2 mb-md-2">
                <v-col cols="12" lg="6" md="8">
                  <label class="para--text ct-label">Full Name</label>
                  <div class="d-flex justify-space-between align-center ct-input-box pa-2 mt-1">
                    <input class="inputs pl-2" placeholder="Full Name" v-model="userInfo.name" />               
                  </div>
                </v-col>
            </v-row>

            <v-row>
                <v-col cols="12" lg="6" md="8">
                <label class="para--text ct-label">Email Address</label>
                  <div class="d-flex justify-space-between align-center ct-input-box pa-2 mt-1">
                    <input class="inputs pl-2" placeholder="Email Address" disabled v-model="userInfo.email" />               
                  </div>
                </v-col>
            </v-row>

            <v-row>
                <v-col cols="12" lg="6" md="8">
                <label class="para--text ct-label">Profession</label>
                  <div class="d-flex justify-space-between align-center ct-input-box pa-2 mt-1">
                    <input class="inputs pl-2" placeholder="Profession" v-model="userInfo.profession" />               
                  </div>
                </v-col>
            </v-row>

            <v-row>
                <v-col cols="12" lg="6" md="8">
                   <label class="para--text ct-label">Subscription</label>
                   <v-switch :value="userInfo.subValue" @change="subPackage" inset dense></v-switch>
                </v-col>
            </v-row>              

          <v-btn height="49" @click="saveCandidate()" :loading="loading" 
                elevation="0" dark 
                class="text-capitalize primary px-8 my-4" style="transform: translateX(-3px)" >
              <span class="d-flex align-center justify-center" >                    
                  <span style="font-size: 0.8rem" class="font-weight-bold">Save</span>
              </span>
          </v-btn> 

        </div>
    </v-container>
</template>

<script>
import axios from 'axios'
import defaultImg from '../assets/social.png'
import { mapGetters } from 'vuex'
import validUserSave from '../validation/validUserSave'

export default {
  data: () => ({
    userInfo: {
        name: '',
        email: '',
        avatar: '',
        profession: '',
        subValue: false
    },
    dialog: false,
    defaultImg: defaultImg,
    loading: false,
    imgLoading: false,
    errors: {}
  }),
  computed: {
    ...mapGetters(['getCandidate','getRouted','getLoading'])
  },
  mounted() {
    
    if(this.getRouted) {
      this.userInfo = { ...this.getCandidate }
    } else {
      this.$once('hook:updated', () => {
        this.userInfo = { ...this.getCandidate }
      })
    }

    const inputFile = document.getElementById('fileUploadImage')

    inputFile.addEventListener("change", async (e) => {

      try {
        if(e.target.files[0].name) {
          let selectedFile = e.target.files[0]
          let fileData = new FormData()

          // Setting the 'image' field and the selected file
          fileData.set(
            'image',
            selectedFile,
            `${selectedFile.lastModified}-${selectedFile.name}`
          )
          this.imgLoading = true

          const response = await axios({
            method: 'post',
            url: '/api/candidate/image',
            data: fileData,
            headers: { 'Content-Type': 'multipart/form-data' },
          })

          let { fileLocation, fileName } = response.data

          this.$store.commit('setCandidate', { ...this.getCandidate, avatar: { url: fileLocation, name: fileName } })

          this.imgLoading = false
        }
      } catch (err) {
        console.log("Err: ", err)
      }
    })
  },
  methods: {
    saveCandidate() {
      this.loading = true

      let userData = {
        name: this.userInfo.name,
        profession: this.userInfo.profession
      }

      if(Object.keys(validUserSave(userData)).length > 0) {
        this.errors = validUserSave(userData)
        return
      }

      axios.post('/api/candidate', userData)
        .then((res) => {
          delete res.data.data.jobs_section
          this.$store.commit('setCandidate', res.data.data)
          this.loading = false
        })
        .catch(err => {
          console.log("Error: ",err)
          this.loading = false
        })
    },
    subPackage() {

      let { subValue, subscription } = this.getCandidate
      
      if(!subValue && subscription.duration < Date.now()) {
        this.$router.push("/subscription")
      } else {
        let newValue = !this.userInfo.subValue
        this.userInfo.subValue = newValue

        axios.post('/api/candidate/swap-sub', { newValue })
      }
    }
  }
}
</script>

<style scoped>

  .ct-name {
      font-size: 1.2rem;
      color: #002178;
  }

  .avatar-container {
    height: 6.25rem;
    width: 6.25rem;
    border-radius: 100%;
    position: relative;
    overflow: hidden;
  }

  .upload_pic-btn {
    position: absolute;
    background-color: rgba(55, 82, 149, 0.527);
    padding: 0rem 2.4rem;
    left: 0;
    bottom: 0;
    transform: translateX(1px);
    border-bottom-left-radius: 10rem;
    border-bottom-right-radius: 10rem;
    cursor: pointer;
    transition: 0.2s;
  }

  .upload_img {
    height: 1.3rem;
    width: auto;
  }

  .inputfile {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
  }

  .ct-load {
    height: 6.2rem;
    width: 6.2rem;
    border-radius: 10rem;
    border: solid 1px #00207825
  }

  .loading-gif {
    height: 2.4rem;
    width: auto;
    opacity: 0.5;
  }

  /* input box style */
  .ct-input-box {
    border: solid 2px #EBEFF5;
    border-radius: 8px;
    height: 3.3rem;
    background-color: #FDFDFD;
  }

  .inputs {
    flex: 1;
    outline: none;
    color: #838CA3;
    font-size: 14px;
    font-weight: 500;
    background-color: #FDFDFD;
  }

  .ct-label {
      font-size: 0.9rem;
  }

  .ct-textarea {
      flex: 1;
      border: solid 2px #EBEFF5;
      outline: none;
      color: #838CA3;
      font-size: 14px;
      font-weight: 500;
      background-color: #FDFDFD;
      padding: 1rem;
      border-radius: 8px;
  }

  textarea:focus{
    outline: 0;
  }

</style>