<template>
    <div>
        <DashBar />
        <div class="space-element"></div>
        <div class="dash-container">
            <div class="tabs-panel">
                <div class="tab-panel-name mb-4 mt-12">User Profile</div>
                <SideNav v-model="activeTab" />
            </div>
            <div class="content-panel">
                <component v-bind:is="currentContent"></component>
            </div>
        </div>
    </div>
</template>

<script>
    import DashBar from '../components/DashBar'
    import SideNav from '../components/SideNav'
    import General from '../components/General'
    import ViewAlert from '../components/ViewAlert'

    export default {
        components: {
            DashBar,
            SideNav,
            General,
            ViewAlert
        },
        data: () => ({
            activeTab: 1
        }),
        computed: {
            currentContent() {
                return this.activeTab === 1 ? 'General' : 'ViewAlert'
            }
        }
    }
</script>

<style scoped>
    .dash-container {
        display: grid;
        grid-template-columns: 1.9fr 8.1fr;
        /* height: 100vh; */
        margin-top: 0px !important;
        /* padding-top: 5.6rem; */
    }

    .tabs-panel {
        border-right: solid 1px #D4DBEC;
    }

    .tab-panel-name {
        font-size: 1.4rem;
        color: rgb(0,33,119);
        text-align: center;
        overflow: hidden;
    }

    .space-element {
        background-color: rgb(255, 255, 255);
        height: 12vh;
        width: 100%;
    }

    .content-panel {
        height: 88vh;
        overflow-y: scroll;
    }

    /* Hide scroll bar */
    .content-panel::-webkit-scrollbar {
        width: 0px;
        background: transparent;
    }

    .content-panel::-webkit-scrollbar-thumb {
        background: #FF0000;
    }
</style>