<template>
    <v-container class="justify-center pa-10 mt-3">
        <div class="mb-12">
          <div class="heading--text ct-heading ct-font mb-2">Job Alert Details</div>
          <div class="para--text ct-para">Here you can complete the information on the type of job you would like, you will then recieve notifications based on your requirements listed here.</div>
        </div>  
        
        <div style="max-width: 44.2rem"
          class="mb-5 d-flex justify-space-between">
          
          <img style="height: 1.6rem; width: auto; cursor: pointer;" @click="addNew"
            src="../assets/new_files/add.png" alt="item" class="search-tag-edit"
          />

          <div class="d-flex">

            <v-tooltip nudge-top="-2" color="#EDF2FF" allow-overflow dark content-class="tooltip"
              top >
              <template v-slot:activator="{ on }">
                
                <img v-on="on"
                 style="height: 1.6rem; width: auto; cursor: pointer;"
                 src="../assets/new_files/sm-icons/email.png" alt="item" class="search-tag-edit"
                /> 

              </template>
              <span class="text--primary">{{getCandidate.job_alert.email}}</span>
            </v-tooltip>
            
            <div>

                <v-dialog
                  v-model="dialog"
                  width="500"
                >
                  <template v-slot:activator="{ on, attrs }">
                    

                    <img  v-bind="attrs"
                      v-on="on"
                      style="height: 1.6rem; width: auto; cursor: pointer;"
                      src="../assets/new_files/sm-icons/edit.png" alt="item" class="search-tag-edit ml-3"
                    />
                  </template>

                  <v-card>
                    <v-card-title
                      class="headline grey lighten-2"
                      primary-title
                    >
                      Add email address
                    </v-card-title>

                    <!-- chip box start -->
                     
                      <!-- <v-combobox
                        v-model="settings.emails"
                        chips
                        flat
                        clearable
                        label="your email"
                        multiple
                        solo
                      >
                        <template v-slot:selection="{ attrs, item, select, selected }">
                          <v-chip
                            v-bind="attrs"
                            color="primary"
                            :input-value="selected"
                            close
                            @click:close="remove(item)"
                            @click="select"
                          >
                            <strong>{{ item }}</strong>&nbsp;
                            
                          </v-chip>
                        </template>
                      </v-combobox> -->

                    <!-- chip box end -->

                    <v-text-field
                      flat
                      label="Solo"
                      placeholder="Email Address for real-time job notification alert"
                      solo
                      v-model='settings.email'
                    ></v-text-field>

                    <v-divider></v-divider>

                    <v-card-actions>
                      <span class="green--text pl-1 body-2" v-if="checkEmail.isValid">{{checkEmail.message}}</span>
                      <span class="red--text pl-1 body-2" v-else>{{checkEmail.message}}</span>
                      <v-spacer></v-spacer>
                      <v-btn
                        color="primary"
                        text
                        @click="addEmail()"
                      >
                        ADD EMAIL
                      </v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
            </div>
             

          </div>
        </div>

        <div>
          <transition-group name="anime">
          <div v-for="(search, index) in searchs" :key="search.custom_id" class=" mb-4 justify-start">
           <SeachBar @deleteIt="deleteData($event)"  
             :search="search" :indexValue="index" />
          </div>
          </transition-group>
        </div>

     
        <v-btn height="46" style="z-index: 0; position: absolute"
            elevation="0" dark  
            :loading='alertLoading'
            @click="addAlertInfo(searchs)"
            class="text-capitalize primary px-6 mt-8" >
            <span class="d-flex align-center justify-center">                    
                <span style="font-size: 0.8rem" class="font-weight-bold">Save</span>
            </span>
        </v-btn> 
    </v-container>
</template>

<script>
import { v4 as uuidv4 } from 'uuid'
import axios from 'axios'
import { mapGetters } from 'vuex'

import SeachBar from './SearchAlert.vue'

//images import 
import skillsImg from '../assets/new_files/skills.png'
import skillsImga from '../assets/new_files/dark/skills.png'
import salaryImg from '../assets/new_files/payment.png'
import salaryImga from '../assets/new_files/dark/payment.png'
import timeImg from '../assets/new_files/time.png'
import timeImga from '../assets/new_files/dark/time.png'
import remoteImg from '../assets/new_files/remote.png'
import remoteImga from '../assets/new_files/dark/remote.png'
import locationImg from '../assets/new_files/location.png'
import locationImga from '../assets/new_files/dark/location.png'
import titleImg from '../assets/new_files/title.png'
import titleImga from '../assets/new_files/dark/title.png'

import validEmail from '../validation/validEmail'


export default {
  data: () => ({
    searchs: [],
    settings: {
      email: '',
    },
    dialog: false,
    alertLoading: false,
    emailErrs: {}
  }),
  components: {
    SeachBar
  },
  computed: {
    ...mapGetters(['getCandidate']),
    checkEmail() {
      if(Object.keys(validEmail(this.settings.email)).length !== 0) {
        let emailErrs = validEmail(this.settings.email)
        return { isValid: false, message: emailErrs.email }
      } else {
        return { isValid: true, message: 'Valid Email' }
      }
    }
  },
  mounted() {
    this.searchs = this.parseOutJob(this.getCandidate.job_alert.searchs)
    this.settings.email = this.getCandidate.job_alert.email
  },
  methods: {
    addAlertInfo() {
      let parseData = this.parseInJob(this.searchs)

      let saveData = { searchs: parseData, email: this.getCandidate.job_alert.email }
      

      this.alertLoading = true
      axios.post('/api/candidate/alert', saveData)
        .then(response => {
          console.log(response)

          this.$store.commit('setCandidate', {
            ...this.getCandidate, 
            job_alert: saveData
          })
          this.alertLoading = false
          
        })
        .catch(err => {
          this.alertLoading = false
          console.log(err)})
    },
    addEmail() {
      if(this.checkEmail.isValid) {
        this.$store.commit('setCandidate', {  
          ...this.getCandidate, 
          job_alert: { email: this.settings.email, searchs: this.getCandidate.job_alert.searchs } 
        })
        
      } else {
        this.settings.email = this.getCandidate.job_alert.email
      }

      this.dialog = false
    },
    addNew() {
      this.searchs.unshift({
        inputs: [
          { id: 'Skills', text: '', placeHolder: 'Skills', activeImg: skillsImga, inactiveImg: skillsImg, _id: 0, hover: false },
          { id: 'Salary', text: '', placeHolder: 'Salary', activeImg: salaryImga, inactiveImg: salaryImg, _id: 1, hover: false },
          { id: 'Job type', text: '', placeHolder: 'Job type', activeImg: timeImga, inactiveImg: timeImg, _id: 2, hover: false },
          { id: 'Remote', text: '', placeHolder: 'Remote', activeImg: remoteImga, inactiveImg: remoteImg, _id: 3, hover: false },
          { id: 'Location', text: '', placeHolder: 'Location', activeImg: locationImga, inactiveImg: locationImg, _id: 4, hover: false },
          { id: 'Job Title', text: '', placeHolder: 'Job title', activeImg: titleImga, inactiveImg: titleImg, _id: 5, hover: false },
        ],
        custom_id: uuidv4()
      })
    },
    deleteData(index) {
      if (index > -1) {
         this.searchs.splice(index, 1)
      }
    },
    parseOutJob(arr) {
      let searchData = arr.map(el => {

        return {
          inputs: [
            { id: 'Skills', text: el.skills, placeHolder: 'Skills', activeImg: skillsImga, inactiveImg: skillsImg, _id: 0, hover: false },
            { id: 'Salary', text: el.salary, placeHolder: 'Salary', activeImg: salaryImga, inactiveImg: salaryImg, _id: 1, hover: false },
            { id: 'Job type', text: el.jobType, placeHolder: 'Job type', activeImg: timeImga, inactiveImg: timeImg, _id: 2, hover: false },
            { id: 'Remote', text: el.remote, placeHolder: 'Remote', activeImg: remoteImga, inactiveImg: remoteImg, _id: 3, hover: false },
            { id: 'Location', text: el.location, placeHolder: 'Location', activeImg: locationImga, inactiveImg: locationImg, _id: 4, hover: false },
            { id: 'Job Title', text: el.title, placeHolder: 'Job title', activeImg: titleImga, inactiveImg: titleImg, _id: 5, hover: false },
          ],
          custom_id: uuidv4()
        }
      })

      return searchData
    },
    parseInJob(arr) {
      
      let parseData = arr.map(el => {
        return {
          skills: el.inputs[0].text,
          jobType: el.inputs[2].text,
          salary: el.inputs[1].text,
          remote: el.inputs[3].text,
          location: el.inputs[4].text,
          title: el.inputs[5].text
        }
      })
    
      return parseData
    }
  }

}
</script>

<style scoped>
 .ct-para {
   font-size: 12px;
 }

 /* animation code */
 .anime-enter-active {
    animation: slide-in 0.2s ease-in forwards;
  }

  .anime-leave-active {
    transition: slide-out 0.2s ease-in forwards;
  }

  @keyframes slide-in {
      from {
          transform: translateY(-50px);
          opacity: 0;
      }
      to {
          transform: translateY(0);
          opacity: 1;
      }
  }

  @keyframes slide-out {
      from {
          transform: translateY(0px);
          opacity: 1;
      }
      to {
          transform: translateY(50px);
          opacity: 0;
      }
  }

/* input box style */
.input-box {
  height: 2rem;
  top: -4rem;
}
  
</style>