<template>
    <div class="search-container d-flex ">

      <div class="d-flex align-center" >
        <div class='search-bar d-flex pa-3 align-center mr-2'>
          <div style="position: relative">

            <div class="d-flex align-center" style="z-index: 0">
              <input class="search-text"
                ref="ctinput"
                :placeholder="search.inputs[activeIndex].placeHolder" 
                @focus="setFocus(true)"
                @blur="setFocus(false)"
                v-model="search.inputs[activeIndex].text" />
              
              <img 
                style="height: 1.4rem; width: auto; position: absolute; right: -1%" 
                src="../assets/new_files/preload.gif" class="ml-1" v-if="search.inputs[4].text.length >= 1 && preload && isFocus" />

            </div>
            
            

            <transition name='anime' type="animation">           
              <div style="z-index: 10"
                  v-if="search.inputs[activeIndex].text.length >= 1 && suggest && isFocus && preIsLoad" >
                  <div class="pretext-p-cover"></div>
                  <div class="pb-4 pretext-p pt-3" >

                  <div v-for="(item, index) in suggest" 
                    @click="addText(item)"
                    :key="index" class="pretext-item px-4 py-1">{{item}}</div>
                </div>
              </div>
            </transition>

            
          </div>

          <div class="v-bar-sm mx-3"></div>

          <div class="hidden-sm-and-down" >
            <div class="d-flex align-center">
              <img v-for="(item, index) in search.inputs" :key="index"
                class="search_tag mr-2" @click="showDrop(index)"
                :src="activeIndex == index ? item.activeImg: item.inactiveImg" 
              />
            </div>
          </div>
          

          <div class="hidden-md-and-up">
            <div style="cursor: pointer;">
              
              
              <div style="position: relative">
                <div class="d-flex align-center" @click="showSmallInput=!showSmallInput">
                  <img :src="search.inputs[activeIndex].activeImg" class='search_tag-active mr-1' />
                  <img src="../assets/new_files/chevron-down.png" alt="dropdown" class="drop-style" />
                </div> 
              

                <transition name='anime' type="animation">  

                  <div style="position: absolute" 
                    class="d-flex flex-column mt-3" v-if="showSmallInput">                 
                    <img class="search_tag-sm mb-2"
                      v-for="item in search_sm" :key="item._id"
                      :class="[
                        item._id != activeIndex ? 'grow' : '',
                      ]"
                      @click="showDrop(item._id)"
                      @mousemove="hoverActive(item._id)"
                      @mouseleave="outDeactive(item._id)"

                      :src="[item.hover ? item.activeImg : item.inactiveImg]" 
                    />                  
                  </div>     

                </transition>  
              </div>
            </div>
          </div>
          
         <div class="v-bar-sm mr-2 ml-1 hidden-sm-and-down"></div>

          <button 
            class="primary hidden-sm-and-down search-btn px-md-8 px-lg-8 px-4 font-weight-bold" 
            @click="searchJob()">Search Now</button>
        </div>

        

          <img style="height: 1.6rem; width: auto" @click="deleteJob(indexValue)"
            src="../assets/new_files/sm-icons/delete.png" alt="item" class="search-tag-edit"
          />
        
      </div>

    </div>
</template>

<script>
import axios from 'axios'
import router from '../router'
import { mapActions, mapGetters } from 'vuex'


//pretext
import {
  jobTitles,
  jobTypes,
  skills,
  salary,
  place
} from '../utils/pretext'

export default {
    props: ['search', 'indexValue'],
    data: () => ({
        activeIndex: 5,
        isFocus: false,
        preload: false,
        dialog: false,
        isHoverTab: false,
        showSmallInput: false
    }),
    computed: {
      ...mapGetters(['getLoading']),
      search_sm() {    
        return this.search.inputs.filter(el => el._id !== this.activeIndex)
      }
    },
    asyncComputed: {
     suggest() {  
          let pres = [skills,salary,jobTypes,['Remote'],place,jobTitles]   

             
           let values = []

           if(this.activeIndex == 4) {

             this.preload = true
             return axios.post('/api/users/place', { data: this.search.inputs[this.activeIndex].text.toLowerCase() })
              .then(result => {
                this.preload = false
                return result.data.sort((a, b) => a.length - b.length).slice(0, 5) || []
              })
              .catch(err => {
                console.log(err)
                this.preload = false
                return []
              })

           } else {
              pres[this.activeIndex].forEach(element => {       
                let lowword = element.toLowerCase()        
                let b = lowword.indexOf(this.search.inputs[this.activeIndex].text.toLowerCase())

                if(b != -1) { values.push(element) }             
              })

              values = values.sort((a, b) => a.length - b.length) || []

             return values.slice(0, 5) || []
         }
      },
      preIsLoad() {
        if(this.activeIndex == 4) {
          return !this.preload
        } else {
          return true
        }
      }
    },
    methods: {
      ...mapActions(['searchJobs', 'updateQuery']),
      addText(text) {
        this.search.inputs[this.activeIndex].text = text   
      },
      hoverActive(index) {
        this.search.inputs[index].hover = true
      },
      outDeactive(index) {
        this.search.inputs[index].hover = false
      },
      setFocus(value) {
        setTimeout(() => {
          this.isFocus = value
        }, 200)
      },
      showDrop(index) {
        
        setTimeout(() => {
            this.showSmallInput = false
            this.activeIndex = index
            this.outDeactive(index)
            if (this.search.inputs[index].text.length < 1) {
                this.$refs.ctinput.focus()
            }
        }, 150)
      },
      deleteJob() {
        this.$emit('deleteIt', this.indexValue)
      },
      async searchJob() {
         let title = this.search.inputs[5].text
         let location = this.search.inputs[4].text || 'any where'

         this.updateQuery({ title, location })

        router.push({ path: '/live-alert' })
      }
    }
}
</script>

<style scoped>
  .search-container {
    position: relative;
  }

  .search-bar {
    box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.07);
    border-left: solid 3px #0046FE;
    height: 2.8rem;
    border-top-right-radius: 2rem;
    border-bottom-right-radius: 2rem;
  }

  .search_tag {
     height: 1.8rem;
     width: auto;
     cursor: pointer;
  }

  .v-bar-sm {
      height: 1.7rem;
      width: 1px;
      background-color: #E7E7E7; 
  }

  .search-text {
    flex: 1;
    color: #757575;
    font-size: 0.9rem;
    outline: none !important;
  }

  @media only screen and (min-width: 600px) {
    .search-text {
      width: 15rem;
    }
   }

  .search-text::placeholder {
    color: #aaaaaa;
  }

  .pretext-p {
    width: 16.6rem; 
    position: absolute;
    transform: translateX(-0.8rem) translateY(0.8rem) ;
    border-bottom-left-radius: 1.2rem;
    border-bottom-right-radius: 1.2rem;
    background-color: white;
    z-index: 15;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.07);
  }

  .pretext-p-cover {
    width: 16.6rem; 
    height: 1.5rem;
    position: absolute;
    transform: translateX(-0.76rem) translateY(0.3rem) ;
    background-color: white;
    z-index: 10;
  }

  .pretext-item {
    color: #949494;
    font-size: 0.88rem;
    cursor: pointer;
  }

  .pretext-item:hover {
    color: white;
    background-color: #0046FE;
    font-size: 0.88rem;
    cursor: pointer;
    
  }

  .tooltip {
    /* font-weight: 700; */
    color: #89AAFF;
    font-size: 0.8rem;
  }

 .search-btn {
    border-radius: 8rem;
    color: white;
    font-size: 0.8rem;  
    height: 2.1rem;
    transform: translateX(5px);
    white-space: nowrap;
  }
.search-tag-edit {
    cursor: pointer;
  }

.add-email-title {
  font-size: 1rem;
  color: #0046FE
}

.add-email-val {
  border: solid 2px #89AAFF;
  padding: 4px;
  font-size: 0.9rem;
  margin: 1rem auto;
  border-radius: 4px;
}


/* animation style */
.anime-enter {
    opacity: 0;
  }

.anime-enter-active {
    animation: slide-in 0.3s ease-out forwards;
    transition: opacity .3s;
    
  }

  .anime-leave-active {
    animation: slide-out 0.3s ease-in forwards;
    transition: opacity 0.3s;
    opacity: 0;
  }

  @keyframes slide-in {
      from {
          transform: translateY(-30px);
      }
      to {
          transform: translateY(0);
      }
  }

  @keyframes slide-out {
      from {
          transform: translateY(0);
      }
      to {
          transform: translateY(-30px);
      }
  }
/* animation style end*/

/* new responsive style */
.search_tag-active{
     height: 1.8rem !important;
     width: 1.8rem !important;
     cursor: pointer;
     /* transform: translateY(5.95rem); */
     padding-right: -1px;
  }
.drop-style {
    height: 1.2rem;
    opacity: 0.7;
    width: auto;
    /* transform: translateY(5.7rem); */
  }
.search_tag-sm {
     height: 1.9rem !important;
     width: 1.9rem !important;
     cursor: pointer;
  }

.grow { transition: all .2s ease-in-out; }
.grow:hover { transform: scale(1.2) }
.grow:active { transform: scale(0.9) }

</style>