<template>
    <v-tabs vertical hide-slider>
        <v-tab 
            class="d-flex justify-start" @click="activateTab(1)" :class="[value == 1 ? 'active_tab' : '']" >
            <span class="d-flex align-center px-4">
                <img src="../assets/general.png" v-if="value == 1" class="tab-img" />
                <img src="../assets/generalnc.png" v-if="value !== 1" class="tab-img" />
                <span class="text-capitalize tab-text ml-6">General</span>
            </span>
        </v-tab>
        <v-tab class="d-flex justify-start"
            @click="activateTab(2)" :class="[value == 2 ? 'active_tab' : '']">
            <span class="d-flex align-center px-4">
                <img src="../assets/new_files/alertc.png" v-if="value == 2" class="tab-img" />
                <img src="../assets/new_files/alert.png" v-if="value != 2" class="tab-img" />
                <span class="text-capitalize tab-text ml-6">View Alerts</span>
            </span>
        </v-tab>
    </v-tabs>
</template>

<script>
    export default {
        props: ['value'],
        data: () => ({
            activeTab: 1
        }),
        methods: {
            activateTab(tab) {
                this.$emit('input', tab)
            }
        },
        beforeCreate() {
            let element = document.createElement("link");
            element.setAttribute("rel", "stylesheet");
            element.setAttribute("type", "text/css");
            element.setAttribute("href", "tabStyle.css");
            element.setAttribute("id", "vertical-tab");
            document.getElementsByTagName("head")[0].appendChild(element);
        }
    }
</script>

<style scoped>
    .active_tab {
        background-color: #F6F9FF !important;
    }

    .tab-text {
        font-size: 1rem !important;
        font-weight: 400;
    }

    /* image styles */
    .tab-img {
        height: 1.2rem;
        width: 1.2rem;
    }

    .tab-img2 {
        height: 1.3rem;
        width: 1.3rem;
    }
</style>

